 .btns button{
  border: none;
  background-color: #040404;
  color: white;
  cursor: pointer;
  padding: 8px 14px;
  margin: 20px;
  height: 50px;
  letter-spacing: 2px;
  text-transform: capitalize;
  box-shadow: 0 15px;
  border: 1px solid black;

  
} 
.imagebox img{
  position: relative;
  height: 100%;
  width: 150%;
  top: -100px;
  right: 10px;
  /* left: 30%; */
  margin-top: 200px;
  transition: transform 8s;
  border: 2px solid white;
  border-radius: 50%;
}
.btns button:hover{
background-color: #7f00ff;
}
.imagebox img:hover{
  position: relative;
  -ms-transform: scale(1.5); /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(1.5); 
}

 




.zoom:hover {
  -ms-transform: scale(2.5); /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(1.5); 
}
@keyframes animateUser {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.5, 1.5);
  }
  100% {
    transform: scale(1, 1);
  }

}
.imagebox #splash{
  position: absolute;
  top: 50%;
  left: 80%;
  transform: translate(-70%,-50%) rotate(-35deg);
  width: 400px;
  filter: saturate(200%);
  height: 400px;
  animation: animate 4s linear infinite;
}
@keyframes animate {
  50%{
    top: 49%;
  left: 79%;
  /* width: 155%; */
  }

}

